@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body{min-height:100vh;min-height:-webkit-fill-available;background:#f7f7f7}html{height:-webkit-fill-available}

#header{display:-webkit-flex;display:flex;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-align-items:center;align-items:center;height:64px;position:-webkit-sticky;position:sticky;z-index:1000;background:#ffffff;box-shadow:0px 2px 4px rgba(0,0,0,0.1)}#header-title{height:24px;font-family:Roboto;font-style:normal;font-weight:bold;font-size:24px;line-height:100%;color:rgba(0,0,0,0.87)}.menu-icon{margin:0 1em}

.sticky{position:fixed;z-index:1000;width:100%}

.wrapper{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;min-height:50vh}.container{position:relative;z-index:1;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;min-width:50vh;margin-top:2em}.welcome-section{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column}p{margin-top:-1em;margin-bottom:3em;font-family:Roboto;font-style:normal;font-weight:normal;font-size:16px;letter-spacing:0.005em;color:rgba(0,0,0,0.87)}h2{font-family:Roboto;font-style:normal;font-weight:bold;font-size:24px;color:rgba(0,0,0,0.87)}button{padding:1em 2.5em;background:#4158c4;border-radius:4px}span{font-family:Roboto;font-weight:500;font-size:1.5em;line-height:100%;letter-spacing:0.0125em;text-transform:capitalize;color:#ffffff;text-align:center}.link{text-decoration:none}.link :hover{cursor:pointer}

.titles{font-family:Roboto;font-style:normal;font-weight:bold;font-size:16px;line-height:100%;letter-spacing:0.005em;color:rgba(0,0,0,0.87)}.new-task-btn{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;padding:1em 0;width:100%;margin:15px 0px;background:rgba(65,88,196,0.1);border-radius:4px;border:none}#btn-text{font-family:Roboto;font-style:normal;font-weight:600;font-size:18px;letter-spacing:0.0125em;text-transform:capitalize;color:#4158c4}.wrapper{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;min-height:50vh}.todos{min-width:50%;margin-top:5em}@media only screen and (max-width: 600px){.todos{max-width:80%}.completed-todos{max-width:80%}}.completed-todos{min-width:50%}.wrapper-completed{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:flex-start;align-items:flex-start;min-height:50vh}.initial-state{font-family:Roboto;font-style:normal;font-weight:bold;font-size:16px;line-height:100%;letter-spacing:0.005em;color:rgba(0,0,0,0.54)}.empty-state-container{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column;margin-bottom:1em}.empty-state-btn-container{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center}

.card{padding:24px;background:#ffffff;box-shadow:0px 2px 4px rgba(0,0,0,0.25);border-radius:16px;width:25em;margin:auto}

.modal{position:fixed;z-index:2000;left:0;top:0;width:100vw;height:100vh;background-color:#000;background-color:rgba(0,0,0,0.4);display:-webkit-flex;display:flex}

.primary-small{padding:16px 18px;background:#4158c4;border-radius:4px;border:none}.primary-small-text{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;height:16px;font-family:Roboto;font-style:normal;font-weight:500;font-size:16px;line-height:100%;letter-spacing:0.0125em;text-transform:capitalize;color:#ffffff;margin:0px 10px}.primary-small-text:hover{cursor:pointer}

.secondary-small{border:none;background-color:#ffffff}.secondary-small-text{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;font-family:Roboto;font-style:normal;font-weight:500;font-size:16px;line-height:100%;letter-spacing:0.0125em;text-transform:capitalize;color:#4158c4;margin:0px 10px}.secondary-small-text:hover{text-decoration:underline;cursor:pointer}

.card p{height:16px;font-family:Roboto;font-style:normal;font-weight:bold;font-size:16px;line-height:100%;letter-spacing:0.005em;color:rgba(0,0,0,0.87);margin:12px 0px}input{width:100%;height:32px;font-family:Roboto;font-style:normal;font-weight:normal;font-size:16px;line-height:100%;letter-spacing:0.005em;color:rgba(0,0,0,0.54);margin:24px 0px;border:none}.btn-div{width:100%;height:32px;display:-webkit-flex;display:flex;-webkit-justify-content:flex-end;justify-content:flex-end}@media only screen and (max-width: 700px){.card{max-width:80%}}.row{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:space-between;justify-content:space-between}.col{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center}

.todo-card{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:flex-start;align-items:flex-start;padding:16px;background:#ffffff;box-shadow:0px 2px 4px rgba(0,0,0,0.25);border-radius:8px;min-width:100%;margin-bottom:0.5em}.card-content{display:-webkit-flex;display:flex;margin:10px 0px}.text-date-section{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-align-items:center;align-items:center;margin:0px 16px;min-width:100%}.text{font-family:Roboto;font-style:normal;font-weight:normal;font-size:16px;line-height:100%;letter-spacing:0.005em;color:rgba(0,0,0,0.87);margin:0px 0px}.ellipse{color:#4158c4}.ellipse-div{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-align-items:center;align-items:center}.completed-text{opacity:50%}

html { 
  box-sizing: border-box; 
} 

html *, html *:before, html *:after { 
  box-sizing: inherit; 
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

